body {
  width: 100%;
  background-color: pink;

}
.navbar {
  width: 100%;
  height: 50px;
  color: purple;
  background-color: pink;
  display: flex;
  justify-content: center;
  align-items: center;
}
span {
  color: sienna;
}
.anggrek {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: bisque;
  color: purple;
  cursor: pointer;
}
.home, .aboutme, .account {
  background-color: lightpink;
  width: 30%;
  height: 40px;
  margin-bottom: 7px;
  margin-top: 7px;
  border-radius: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
}
img {
  border: 2px solid blueviolet;
  border-radius: 20%;
  width: 300px;
  height: 450px;
}
.tampilanhome {
  height: 100%;
  background-color: bisque;
  display: flex;
  flex-direction: row;
  justify-content:space-around;
  align-items: center;
  margin-top: 150px;
  box-shadow: 0 3px 3px 0 violet;
}
h1 {
  margin-left: 15%;
}
.link {
  text-decoration: none;
  color: firebrick;
}